export default{
	props: {
		types: {
			type: String,
			default: 'image'
		},
		dataList: {
			type: Array,
			default: function() {
				return []
			}
		},
		clearIcon: {
			type: String,
			default: 'http://img1.imgtn.bdimg.com/it/u=451604666,2295832001&fm=26&gp=0.jpg'
		},
		uploadUrl: {
			type: String,
			default: ''
		},
		deleteUrl: {
			type: String,
			default: ''
		},
		uploadCount: {
			type: Number,
			default: 1
		},
		//上传图片大小 默认3M
		upload_max: {
			type: Number,
			default: 3
		}
	},
	data(){
		return {
			//上传的图片地址
			uploadImages: [],
			//展示的图片地址
			uploads: [],
			// 超出限制数组
			exceeded_list: [],
		}
	},
	watch:{
		dataList() {
			this.uploads = this.dataList
		}
	},
	methods:{
		previewImage (e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.dataList
			})
		},
		chooseUploads(){
			switch (this.types){
				case 'image': 
					uni.chooseImage({
						count: this.uploadCount - this.uploads.length, //默认9
						sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
						sourceType: ['album', 'camera'], //从相册选择
						success: (res) => {
							for(let i = 0; i< res.tempFiles.length; i++){
								if(Math.ceil(res.tempFiles[i].size / 1024) < this.upload_max * 1024){
									this.uploads.push(res.tempFiles[i].path)
									this.uploadImages.push(res.tempFiles[i].path)
								}else {
									this.exceeded_list.push(i === 0 ? 1 : i + 1);
									uni.showModal({
										title: '提示',
										content: `第${[...new Set(this.exceeded_list)].join(',')}张图片超出限制${this.upload_max}MB,已过滤`
									});
								}
							}
						},
						fail: (err) => {
							uni.showModal({
								content: JSON.stringify(err)
							});
						}
					});
				break;
				case 'video' :
					uni.chooseVideo({
						sourceType: ['camera', 'album'],
						success: (res) => {
							console.log(res)
							if(Math.ceil(res.size / 1024) < this.upload_max * 1024){
								this.uploads.push(res.tempFilePath)
								uni.uploadFile({
									url: this.uploadUrl, //仅为示例，非真实的接口地址
									filePath: res.tempFilePath,
									name: 'file',
									//请求参数
									formData: {
										'user': 'test'
									},
									success: (uploadFileRes) => {
										this.$emit('successVideo',uploadFileRes)
									}
								});
							}else {
								uni.showModal({
									title: '提示',
									content: `第${[...new Set(this.exceeded_list)].join(',')}张视频超出限制${this.upload_max}MB,已过滤`
								});
							}
						},
						fail: (err) => {
							uni.showModal({
								content: JSON.stringify(err)
							});
						}
					});
				break;
			}
		},
		delImage(index){
			if(this.uploads[index].substring(0,4) !== 'http'){
				this.uploads.splice(index,1)
				return;
			};
			if(!this.deleteUrl) {
				uni.showModal({
					content: '请填写删除接口'
				});
				return;
			};
			uni.request({
				url: this.deleteUrl,
				method: 'DELETE',
				data: {
					image: this.dataList[index]
				},
				success: res => {
					this.uploads.splice(index,1)
				},
			});
		},
		upload(){
			if(!this.uploadUrl) {
				uni.showModal({
					content: '请填写上传接口'
				});
				return;
			};
			for (let i of this.uploadImages) {
				uni.uploadFile({
					url: this.uploadUrl, //仅为示例，非真实的接口地址
					filePath: i,
					name: 'file',
					//请求参数
					formData: {
						'user': 'test'
					},
					success: (uploadFileRes) => {
						this.$emit('successImage',uploadFileRes)
					}
				});
			}
		}
	}
}
